/* Layout general */
body, .app {
  background: #0c243d;
  color: #fff;
  font-family: 'Segoe UI', sans-serif;
}

/* Navbar */
.nav-bar {
  display: flex;
  justify-content: space-around;
  background: #2f3d5e;
  padding: 10px;
}
.nav-bar button {
  background: none;
  color: #bbb;
  border: none;
  font-size: 16px;
}
.nav-bar button.active {
  color: #d6b86a;
}

/* Recomendaciones */
.recommendations h2,
.section h2 {
  padding: 20px;
}

.dish-card {
  display: flex;
  background: #384766;
  margin: 10px;
  padding: 10px;
  border-radius: 20px;
}
.dish-card img {
  width: 80px;
  height: 80px;
  border-radius: 12px;
  object-fit: cover;
}
.card-content {
  margin-left: 16px;
  flex: 1;
}
.card-footer {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 8px;
}

/* Secciones normales */
.subsection h3 {
  color: #d6b86a;
  margin-left: 20px;
  margin-top: 20px;
}

.dish-item {
  display: flex;
  justify-content: space-between;
  background: #2f3d5e;
  margin: 10px 20px;
  padding: 14px;
  border-radius: 20px;
}

.dish-info h4 {
  color: #d6b86a;
  margin: 0;
}
.dish-info p {
  margin: 4px 0 0;
  color: #ccc;
  font-size: 14px;
}

.dish-actions {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: flex-end;
}
.price {
  margin-bottom: 8px;
}

/* Bot√≥n AR */
.ar-btn {
  background: #f15454;
  color: white;
  padding: 6px 14px;
  border-radius: 20px;
  border: none;
  font-weight: bold;
  cursor: pointer;
}
.ar-btn:hover {
  background: #d94141;
}
